@import "../reset.scss";

.module {
  // ==================================================================
  @include smallDevice {
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    box-sizing: border-box;
    overflow: hidden;
    text-decoration: none;
    position: relative;

    .status {
      display: flex;
      width: 100%;
      justify-content: flex-start;
      font-weight: 200;
      font-size: 40px;
      position: absolute;
      top: 20px;
      left: 10px;
      z-index: 3;

      .reserved {
        border-radius: 50px;
        background: linear-gradient(
          109.6deg,
          rgba(255, 220, 47, 0.85) 11.2%,
          rgba(245, 253, 0, 0.85) 100.2%
        );

        padding: 10px 30px;
        display: flex;
        align-items: center;
        gap: 20px;
        h1 {
          &:first-child {
            color: black;
            font-weight: 800;
          }
        }
        div {
          color: rgb(1, 1, 70);
          background-color: rgba(255, 255, 255, 0.9);
          border-radius: 50%;
          width: 42px;
          height: 42px;
          display: flex;
          justify-content: center;
          align-items: center;
          font-weight: 600;
          font-size: 40px;
        }
      }

      .none {
        border-radius: 20px;
        padding: 5px 10px;
        color: transparent;
        background-color: transparent;
      }
    }

    .image {
      height: 600px;
      width: 100%;
      overflow: hidden;
      display: flex;
      justify-content: center;
      align-items: center;

      img {
        height: 100%;
        width: 100%;
        transition: transform 0.5s ease;
        transform-origin: center center;
        justify-self: center;
        align-self: center;

        &:hover {
          transform: scale(1.05);
        }
      }
    }

    .information {
      width: 100%;
      display: flex;
      justify-content: space-between;
      font-weight: bold;
      padding: 20px 30px;
      box-sizing: border-box;
      font-size: 60px;

      .title {
        color: black;
      }

      .price {
        display: flex;
        justify-content: flex-end;
        gap: 3px;
        .price_old {
          text-decoration: line-through;
          color: rgb(177, 177, 177);
          font-size: 45px;
          font-weight: 400;
        }
        .price_new {
          color: rgb(187, 28, 28);
        }
      }
    }

    .availability {
      width: 100%;
      display: flex;
      justify-content: flex-start;
      padding: 0px 35px;
      margin-top: -5px;
      box-sizing: border-box;
      h1 {
        font-style: italic;
        font-size: 40px;
        font-weight: 350;
        color: rgb(90, 90, 90);
        &.avail_active {
          color: rgb(167, 46, 46);
          font-weight: 500;
        }
      }
    }
  }

  // ==================================================================
  // ==================================================================
  // ==================================================================
  @include mediumDevice {
    // width: 1fr;
    max-width: 430px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    box-sizing: border-box;
    overflow: hidden;
    text-decoration: none;

    .status {
      display: flex;
      width: 100%;
      justify-content: flex-start;
      font-weight: 200;
      font-size: 10px;
      position: relative;
      background-color: red;
      height: 0;
      z-index: 3;

      .reserved {
        border-radius: 20px;
        background: linear-gradient(
          109.6deg,
          rgba(255, 220, 47, 0.85) 11.2%,
          rgba(245, 253, 0, 0.85) 100.2%
        );

        padding: 5px 10px;
        display: flex;
        position: absolute;
        gap: 10px;
        top: -10px;
        left: 0;
        h1 {
          &:first-child {
            color: black;
            font-size: 15px;
            font-weight: 600;
          }
        }
        div {
          color: rgb(1, 1, 70);
          background-color: rgba(255, 255, 255, 0.9);
          border-radius: 50%;
          width: 16px;
          height: 16px;
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 14px;
          font-weight: 600;
        }
      }

      .none {
        border-radius: 20px;
        padding: 5px 10px;
        color: transparent;
        background-color: transparent;
        top: 0;
      }
    }

    .image {
      height: 250px;
      width: 100%;
      overflow: hidden;
      display: flex;
      justify-content: center;
      align-items: center;

      img {
        height: 100%;
        width: auto;
        transition: transform 0.5s ease;
        transform-origin: center center;
        justify-self: center;
        align-self: center;

        &:hover {
          transform: scale(1.05);
        }
      }
    }

    .information {
      width: 100%;
      display: flex;
      justify-content: space-between;
      font-weight: bold;
      padding: 10px 15px;
      box-sizing: border-box;
      font-size: 25px;

      .title {
        color: black;
      }

      .price {
        display: flex;
        justify-content: flex-end;
        gap: 5px;
        .price_old {
          text-decoration: line-through;
          color: lightgray;
          font-size: 20px;
        }
        .price_new {
          color: rgb(187, 28, 28);
        }
      }
    }

    .availability {
      width: 100%;
      display: flex;
      justify-content: flex-start;
      padding: 0px 15px;
      margin-top: -5px;
      box-sizing: border-box;
      h1 {
        font-style: italic;
        font-size: 19px;
        font-weight: 200;
        color: rgb(90, 90, 90);
        &.avail_active {
          color: rgb(167, 46, 46);
          font-weight: 500;
        }
      }
    }
  }
}
