@import "../reset.scss";

.page {
  @include smallDevice {
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;

    .header {
      height: 100%;
      width: 100%;
      background-color: rgb(0, 0, 0);
      display: flex;
      justify-content: center;
      align-items: center;
      .slide {
        height: auto;
        width: 100%;
        background-color: black;
        img {
          height: auto;
          width: 100%;
        }
      }
    }

    .description {
      width: 100%;
      display: flex;
      flex-direction: column;
      padding-top: 40px;
      .des_header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 20px;
        .header_title {
          display: flex;
          flex-direction: column;
          gap: 10px;
          justify-content: flex-start;
          margin: 0;
          .title_h1 {
            text-transform: capitalize;
            font-size: 70px;
            height: fit-content;
            margin: 0;
            padding-top: 10px;
          }
          .title_h2 {
            font-size: 50px;
            margin: 0;
            color: gray;
            font-weight: 300;
            text-indent: 10px;
          }
        }
        .header_price {
          display: flex;
          justify-content: flex-start;
          gap: 20px;
          .price_old {
            color: rgb(147, 147, 147);
            text-decoration: line-through;
            font-size: 45px;
            font-weight: 300;
          }
          .price_new {
            color: rgb(199, 22, 22);
            font-size: 80px;
          }
        }
      }
      .des_body {
        padding: 90px 20px 10px 20px;
        display: flex;
        flex-direction: column;
        gap: 10px;
        .info {
          display: flex;
          justify-content: space-between;
          border-bottom: solid 1px lightgray;
          color: gray;
          padding: 20px;
          box-sizing: border-box;

          .info_left {
            font-size: 50px;
            font-weight: 400;
          }
          .info_this {
            font-size: 50px;
            font-weight: 300;
          }
        }

        .info_note {
          padding: 20px;
          box-sizing: border-box;
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          align-items: flex-start;
          color: gray;
          gap: 25px;

          .invisible {
            display: none;
          }
          .note {
            font-size: 48px;
            font-weight: 300;
            &::before {
              content: "✓ ";
            }
          }
        }
      }

      .des_form {
        display: flex;
        flex-direction: column;
        gap: 80px;
        margin-top: 100px;
        margin-bottom: 150px;
        padding: 50px 50px;
        font-size: 50px;
        color: gray;
        font-weight: 400;
        box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
        .reservation {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          align-items: flex-start;
          gap: 40px;
          .block {
            display: flex;
            flex-direction: column;
            gap: 15px;
            input,
            select {
              border: solid 1px #dddddd;
              border-radius: 5px;
              width: 90vw;
              height: 60px;
              padding: 10px 10px;
              box-sizing: border-box;
              font-size: 40px;
              color: rgb(80, 80, 80);
              &:focus {
                box-shadow: none;
                border: 1px solid #ebc460;
              }
              &::placeholder {
                font-size: 40px;
                font-weight: 300;
              }
            }
            select {
              background-color: rgba(255, 255, 255, 0.422);
              width: 90vw;
              height: 70px;
              padding: 10px 10px;
              font-size: 40px;
              color: gray;
              font-weight: 300;
            }
            .required {
              &::after {
                content: "*";
                color: red;
              }
            }
          }

          .btn_block {
            width: 100%;
            display: flex;
            justify-content: center;
            .button {
              width: 500px;
              padding: 15px 20px;
              text-align: center;
              text-transform: capitalize;
              transition: 0.5s;
              background-size: 200% auto;
              color: white;
              border-radius: 10px;
              display: block;
              border: 0px;
              font-size: 40px;
              font-weight: 500;
              box-shadow: 0px 0px 14px -7px #f09819;
              background-image: linear-gradient(
                45deg,
                #ff512f 0%,
                #f09819 51%,
                #ff512f 100%
              );
              cursor: pointer;
              user-select: none;
              -webkit-user-select: none;
              touch-action: manipulation;
            }

            .button:hover {
              background-position: right center;
              color: #fff;
              text-decoration: none;
            }

            .button:active {
              transform: scale(0.95);
            }
          }
        }
        .rule {
          display: flex;
          flex-direction: column;
          gap: 10px;
          line-height: 65px;
          font-size: 50px;
          font-weight: 350;
          p {
            color: gray;
            text-indent: -50px;
            margin-left: 45px;
            &::before {
              content: "◻︎\00a0";
            }
            span {
              text-decoration: underline;
              text-decoration-thickness: 2px;
              text-underline-offset: 5px;
            }
          }
        }
        .waitlist {
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          gap: 10px;
          padding-bottom: 50px;
          .wait_header {
            display: flex;
            align-items: center;
            gap: 8px;
            h1 {
              font-size: 50px;
              color: gray;
              font-weight: 400;
              padding: 20px 0px;
            }
            // div {
            //   height: 15px;
            //   width: 15px;
            //   font-size: 10px;
            //   background-color: slategray;
            //   color: white;
            //   font-weight: 400;
            //   display: flex;
            //   justify-content: center;
            //   align-items: center;
            //   border-radius: 50%;
            //   position: relative;
            // }
          }
          .wait_turn {
            padding: 10px 20px;
            background-color: transparent;
            border: none;
            margin: 0;
            text-align: inherit;
            font: inherit;
            border-radius: 0;
            appearance: none; // Just in case we missed anything.
            cursor: pointer;
            display: grid;
            grid-template-columns: 50px 3fr 3fr 2fr;

            &:nth-child(2n) {
              background-color: #e5e5e553;
            }
            h1 {
              font-size: 42px;
              font-weight: 350;
              text-transform: capitalize;
              text-wrap: nowrap;
            }
            &:hover {
              box-shadow: rgba(33, 35, 38, 0.1) 0px 10px 10px -10px;
            }
          }

          .canceled {
            text-decoration: line-through;
            text-decoration-color: rgb(219, 0, 0);
            h1 {
              text-decoration-thickness: 0.3px;
              color: rgb(219, 0, 0);
            }
          }
        }
      }
    }
  }

  // ===============================================================
  // ===============================================================
  // ===============================================================
  @include mediumDevice {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;

    .header {
      height: 80vh;
      width: 100%;
      background-color: rgb(0, 0, 0);
      display: flex;
      justify-content: center;
      align-items: center;
      overflow: hidden;
      .slide {
        height: auto;
        width: 90%;
        background-color: black;
        img {
          height: auto;
          width: 100%;
        }
      }
    }

    .description {
      width: 80%;
      display: flex;
      flex-direction: column;
      padding-top: 10px;
      .des_header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .header_title {
          display: flex;
          flex-direction: column;
          gap: 3px;
          justify-content: flex-start;
          margin: 0;
          .title_h1 {
            text-transform: capitalize;
            font-size: 60px;
            height: fit-content;
            margin: 0;
            padding-top: 10px;
          }
          .title_h2 {
            font-size: 30px;
            margin: 0;
            color: gray;
            font-weight: 400;
            text-indent: 0;
          }
        }
        .header_price {
          display: flex;
          justify-content: flex-end;
          gap: 10px;
          .price_old {
            color: rgb(147, 147, 147);
            text-decoration: line-through;
            font-size: 45px;
            font-weight: 400;
          }
          .price_new {
            color: rgb(199, 22, 22);
            font-size: 65px;
          }
        }
      }
      .des_body {
        padding: 50px 20px 10px 20px;
        display: flex;
        flex-direction: column;
        gap: 10px;
        .info {
          display: flex;
          justify-content: space-between;
          border-bottom: solid 1px lightgray;
          color: gray;
          padding: 20px;
          box-sizing: border-box;

          .info_left {
            font-size: 30px;
            font-weight: 400;
          }
          .info_this {
            font-size: 28px;
            font-weight: 300;
          }
        }

        .info_note {
          padding: 20px;
          box-sizing: border-box;
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          align-items: flex-start;
          color: gray;
          gap: 15px;

          .invisible {
            display: none;
          }
          .note {
            font-size: 26px;
            font-weight: 300;
            &::before {
              content: "✓ ";
            }
          }
        }
      }

      .des_form {
        align-self: center;
        display: flex;
        flex-direction: column;
        gap: 60px;
        margin: 30px 10px 50px 10px;
        padding: 50px 80px;
        font-size: 26px;
        color: gray;
        font-weight: 300;
        box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
        .reservation {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          align-items: flex-start;
          gap: 20px;
          .block {
            display: flex;
            flex-direction: row;
            width: 100%;
            justify-content: space-between;
            gap: 5px;
            input,
            select {
              border: solid 1px #dddddd;
              border-radius: 5px;
              width: 350px;
              height: fit-content;
              padding: 5px 10px;
              font-size: 18px;
              font-weight: 300;
              color: rgb(71, 71, 71);
              box-sizing: border-box;
              &:focus {
                box-shadow: none;
                border: 1px solid #ebc460;
              }
              &::placeholder {
                font-size: 17px;
                font-weight: 100;
                color: rgb(160, 160, 160);
              }
            }
            select {
              background-color: rgba(255, 255, 255, 0.422);
              background-color: white;
              padding: 5px 10px;
              font-size: 17px;
              color: rgb(160, 160, 160);
              font-weight: 300;
              box-sizing: border-box;
            }
            .required {
              &::after {
                content: "*";
                color: red;
              }
            }
          }

          .btn_block {
            width: 100%;
            display: flex;
            justify-content: flex-end;
            .button {
              width: 250px;
              text-align: center;
              text-transform: capitalize;
              transition: 0.5s;
              background-size: 200% auto;
              color: white;
              border-radius: 10px;
              display: block;
              font-size: 22px;
              padding: 10px 30px;
              margin-right: 5px;
              font-weight: 500;
              box-shadow: 0px 0px 14px -7px #f09819;
              background-image: linear-gradient(
                45deg,
                #ff512f 0%,
                #f09819 51%,
                #ff512f 100%
              );
              cursor: pointer;
              user-select: none;
              -webkit-user-select: none;
              touch-action: manipulation;
            }

            .button:hover {
              background-position: right center;
              color: #fff;
              text-decoration: none;
            }

            .button:active {
              transform: scale(0.95);
            }
          }
        }

        .rule {
          display: flex;
          flex-direction: column;
          gap: 20px;
          line-height: normal;
          p {
            color: gray;
            text-indent: -25px;
            margin-left: 25px;
            font-size: 22px;
            font-weight: 300;
            &::before {
              content: "✓ ";
            }
            span {
              text-decoration: underline;
              text-decoration-thickness: 1px;
            }
          }
        }
        .waitlist {
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          gap: 0px;
          .wait_header {
            display: flex;
            align-items: center;
            gap: 8px;
            h1 {
              font-size: 25px;
              color: gray;
              font-weight: 300;
              padding: 20px 0px;
            }
            div {
              height: 15px;
              width: 15px;
              font-size: 10px;
              background-color: slategray;
              color: white;
              font-weight: 400;
              display: flex;
              justify-content: center;
              align-items: center;
              border-radius: 50%;
              position: relative;
            }
          }
          .wait_turn {
            padding: 10px 20px;
            background-color: transparent;
            border: none;
            margin: 0;
            text-align: inherit;
            font: inherit;
            border-radius: 0;
            appearance: none; // Just in case we missed anything.
            cursor: pointer;
            display: grid;
            grid-template-columns: 30px 2fr 3fr 3fr;

            &:nth-child(2n) {
              background-color: #e5e5e553;
            }
            h1 {
              font-size: 22px;
              font-weight: 100;
              text-transform: capitalize;
              text-wrap: nowrap;
            }
            &:hover {
              box-shadow: rgba(33, 35, 38, 0.1) 0px 10px 10px -10px;
              background-color: rgb(255, 255, 236);
            }
          }

          .canceled {
            text-decoration: line-through;
            text-decoration-color: rgb(219, 0, 0);
            h1 {
              text-decoration-thickness: 0.3px;
              color: rgb(219, 0, 0);
            }
          }
        }
      }
    }
  }
}
