@import "../reset.scss";

.app {
  box-sizing: border-box;

  // ===================================================================
  @include smallDevice() {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 0px 30px 30px 30px;

    .header {
      display: flex;
      justify-content: center;
      align-items: center;
      height: fit-content;
      width: 100vw;
      padding: 200px 0px;
      color: white;
      background-image: url("https://st4.depositphotos.com/1005515/28446/i/450/depositphotos_284465010-stock-photo-simple-blurred-abstract-background.jpg");
      background-size: cover;
      h1 {
        margin: 0px;
        font-weight: 900;
        font-size: 90px;
        text-transform: uppercase;
        text-align: center;
      }
    }

    .keynote {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      text-wrap: nowrap;
      padding: 100px 20px;
      margin-top: 30px;
      border-top: 1px solid #093857;
      border-bottom: 1px solid #093857;
      gap: 50px;
      .info {
        display: grid;
        grid-template-columns: 0.34fr 0.6fr;
        h1 {
          text-transform: uppercase;
          font-size: 35px;
          color: #093857;
          font-weight: 900;
          text-indent: 10px;
        }
        p {
          align-self: center;
          font-size: 33px;
          color: rgb(69, 69, 69);
        }
        a {
          align-self: center;
          text-decoration: none;
        }
        &:nth-child(2) {
          p {
            font-style: italic;
          }
        }
      }

      .note {
        h1 {
          text-transform: uppercase;
          font-size: 32px;
          color: #093857;
          font-weight: 900;
          // align-self: flex-start;
          justify-self: flex-end;
        }
      }
    }

    .shopping {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      gap: 100px;
    }
  }

  // ===================================================================
  // ===================================================================
  // ===================================================================
  @include mediumDevice() {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 0px 30px 30px 30px;

    .header {
      display: flex;
      justify-content: center;
      align-items: center;
      height: fit-content;
      width: 100vw;
      padding: 150px 0px;
      color: white;
      background-image: url("https://st4.depositphotos.com/1005515/28446/i/450/depositphotos_284465010-stock-photo-simple-blurred-abstract-background.jpg");
      background-size: cover;
      h1 {
        margin: 0px;
        font-weight: 900;
        font-size: 80px;
        text-transform: uppercase;
        text-align: center;
      }
    }

    .keynote {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      text-wrap: nowrap;
      padding: 50px 100px;
      margin-top: 30px;
      border-top: 1px solid #093857;
      border-bottom: 1px solid #093857;
      .info {
        display: grid;
        grid-template-columns: 0.4fr 0.6fr;
        h1 {
          text-transform: uppercase;
          font-size: 25px;
          color: #093857;
          font-weight: 900;
          text-indent: 20px;
        }
        p {
          align-self: center;
          font-size: 22px;
          font-weight: 300;
          color: rgb(69, 69, 69);
        }
        a {
          align-self: center;
          text-decoration: none;
        }
        &:nth-child(2) {
          p {
            font-style: italic;
          }
        }
      }
      .note {
        h1 {
          text-transform: uppercase;
          font-size: 25px;
          color: #093857;
          font-weight: 900;
          // align-self: flex-start;
          justify-self: flex-end;
        }
      }
    }

    .shopping {
      padding: 20px 20px;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      gap: 20px;
    }
  }
}
